<template>
    <!-- <Mycircle :diam=300 :n=100></Mycircle>
    <Mycircle :diam=250 :n=30></Mycircle>
    <Mycircle :diam=200 :n=7></Mycircle> -->
    
    <div id="Circle_Maker">

        <div class="test" :style="`text-align:center; padding-top:60px;`">
            <p :style="``">년, 월, 주, 일, 시</p>
            <p :style="`display:inline-block; font-size:35px;`">미리 연마</p>
            
        </div>

        <div id="maker"
        :style="`
        height:${diam}px;
        margin: 20px;
        position: relative;
        `">
        
            <Mycircle v-for="(item,i) in Circle_Maker" :key="i"
                :diam=diam-(i*50) :Items=item.items :On_i=item.On_i :Degree=item.Degree :FontSize=item.FontSize :grad=70
            />
        </div>


        <p v-for="(item,i) in Circle_Maker" :key="i"
        :style="`text-align:center;`">{{item.display}} :
            
            <span
            :style="`text-align:center; color:#ffee10;`"
            >{{item.remainH}}</span>

            <ProgressBar_Linear :Amount=item.remainMS :Total=item.totalMS :style="`margin:0 20px;`"></ProgressBar_Linear>            
         </p>
         

        
        


        <!-- <Mycircle v-for="(item,i) in Circle_Maker" :key="i"
            :diam=diam-((5-i)*50) :Items=item.items :On_i=item.On_i :Degree=item.Degree
        ></Mycircle> -->
        <!-- <Mycircle :style="`top:0px; left:20px;`" :diam=150 :Items=days.items :On_i=days.On_i :Degree=days.Degree></Mycircle> -->
    </div>


    

</template>
<script>
import Mycircle from './Mycircle.vue'
import ProgressBar_Linear from './ProgressBar_Linear.vue'

export default {
    mounted() {  
        this.setThisday();
        this.setRemain();

        this.setDiam();
    },  
    
    data(){
        return{
            birth:new Date('1992-1-9 05:00:00'),
            death:new Date('2073-10-14 05:00:00'),
            today:new Date(),
            diam:100,
            years : {FirstDate:Date, LastDate:Date, remainMS: Number, remainH: Number, display:"이번 생 남은 일",name:"년", items: [],On_i : Number,Degree :Number,FontSize:7},
            // months : {items: [],On_i: Number,Degree :Number},
            // months : {items: ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec",]
            months :{FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"올 해 남은 일 수",name:"월",items: [],On_i: Number,Degree :Number,FontSize:13} ,
            // '1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월']
            date : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"이 달 남은 시간",ame:"일",items: [],On_i: Number,Degree :Number,FontSize:13},            
            days : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"이번 주 남은 시간",name:"주",items: ["월","화","수","목","금","토","일"],On_i: Number,Degree :Number,FontSize:13} ,
            // days : {items: ["Mon","Tue","Wed","Thur","Fri","Sat","Sun"],On_i: Number,Degree :Number,FontSize:13} ,
            hours : {FirstDate:Date, LastDate:Date,remainMS: Number, remainH: Number,display:"오늘 남은 시간", name:"시",items: [],On_i: Number,Degree :Number,FontSize:8},          
            
            Circle_Maker : [],
            // day :[],
            // 
            // days : ["월","화","수","목","금","토","일"],
        }
    },
    methods : {
        setDiam()
        {
            if(window.screen.width<window.screen.height)
                this.diam = window.screen.width-50;
            else
                this.diam = window.screen.height-300;
        },
        // getYears(birth){
        //     // for(var i=birth;i<birth+100;i+=3)
        //     var currentTime = new Date();
        //     var cnt = 0;
        //     for(var i=birth;i<birth+81;i+=5)//92년생 남 기대수명. 
        //     {
        //         this.years.items.push(i);
        //         // this.years.items.push((i%100)+'년');
        //         if(i==currentTime.getFullYear())
        //            this.years.On_i = cnt;
        //         cnt ++;
        //     }
        //     },
        setThisday()
            {
                // this.getYears(birth);                
                
                // for(var i=1;i<=31;i++)this.date.items.push(i);//+'일');
                //눈금은 있되, 글자는 없게하기. 
                for(var i=this.birth.getFullYear();i<this.death.getFullYear()-2;i+=10)this.years.items.push(i+'년');
                for(i=1;i<=12;i+=2)this.months.items.push(i+'월');
                for(i=1;i<=31;i+=7)this.date.items.push(i+'일');
                for(i=0;i<=23;i+=3)this.hours.items.push(i);
                // for(i=0;i<=12;i++)this.hours.items.push(i+'am');
                // for(i=1;i<=11;i++)this.hours.items.push(i+'pm');


                var currentTime = new Date();
                this.months.On_i= currentTime.getMonth();
                this.date.On_i= currentTime.getDate()-1;
                this.days.On_i= currentTime.getDay()-1;
                this.hours.On_i= currentTime.getHours()-1;

                
                // this.years.Degree= ((currentTime.getFullYear()-this.birth.getFullYear())/81)*360;//평균수명.
                // this.months.Degree= (currentTime.getMonth())/12*360;
                // this.date.Degree= (currentTime.getDate())/30*360;
                // this.days.Degree= (currentTime.getDay()/7)*360;
                // this.hours.Degree= (currentTime.getHours())/24*360;

                this.Circle_Maker.push(this.years);
                this.Circle_Maker.push(this.months);
                this.Circle_Maker.push(this.date);
                this.Circle_Maker.push(this.days);
                this.Circle_Maker.push(this.hours);
                
                // this.Circle_Maker.push(this.hours);
                // this.Circle_Maker.push(this.days);
                // this.Circle_Maker.push(this.date);
                // this.Circle_Maker.push(this.months);
                // this.Circle_Maker.push(this.years);
            },
        setRemain()
            {               
                this.years.LastDate = this.death;
                this.months.LastDate = new Date(this.today.getFullYear()+1,0,1);//올해 말.
                this.date.LastDate = new Date(this.today.getFullYear(), this.today.getMonth()+1, 1);//이달 말.
                var nextMon = new Date();
                nextMon.setDate(this.today.getDate() + (((1 + 7 - this.today.getDay()) % 7) || 7));//다음월요일 00시.
                nextMon.setHours(0,0,0);
                this.days.LastDate = nextMon;//담주 월.
                var nextDay =  new Date(this.today.getFullYear(), this.today.getMonth(), this.today.getDate()+1)
                nextDay.setHours(0,0,0);
                this.hours.LastDate = nextDay;//내일.

                this.years.FirstDate = this.birth;
                this.months.FirstDate = new Date(this.today.getFullYear(),0,1);//올해 초.
                this.date.FirstDate = new Date(this.today.getFullYear(), this.today.getMonth(), 1);//이달 초.
                nextMon = new Date();
                nextMon.setDate(this.today.getDate() - (((1 + 7 - this.today.getDay()) % 7) || 7));//다음월요일 00시.
                nextMon.setHours(0,0,0);
                this.days.FirstDate = nextMon;//금주 월.
                nextDay =  new Date(this.today);
                nextDay.setHours(0,0,0);
                this.hours.FirstDate = nextDay;//오늘. 



                this.Circle_Maker.forEach(item => {
                    // console.log(item.remainMS);
                    item.remainMS = item.LastDate-this.today;
                    item.totalMS = item.LastDate-item.FirstDate;
                    // console.log(this.msToTime(item.totalMS));
                    item.remainH = this.msToTime(item.remainMS) +" / "+this.msToTime(item.totalMS);

                    item.Degree = ((item.totalMS-item.remainMS)/item.totalMS)*360;
                    console.log(item.Degree);
                });
            },
        msToTime(duration) {
            // var milliseconds = parseInt((duration % 1000) / 100),
            var minutes = Math.floor((duration / (1000 * 60)) % 60);
            var hours = Math.floor((duration / (1000 * 60 * 60)));
            // var hours = Math.floor((duration / (1000 * 60 * 60)) % 24);
            // var days = Math.floor((duration / (1000 * 60 * 60* 24)) % 24);
            var result ="";
            // result +=(days>0)? (days+"일 ") : "";
            result += hours+":"+minutes;
            return result;
        }
  },
  components: { Mycircle , ProgressBar_Linear},
    
}
</script>
<style>
#Circle_Maker
{
    position: relative;
    /* margin: auto; */
    /* height: 100vh; */
    
}

p{
    color:white;
}
   

</style>